import workerFunc from './workerScript';
import { compose } from 'ramda';
import { transformEvalableToUrl, formatFuntionToEvalable } from './util';
import { proxy } from 'workly';

// transfrom your code to and url that can be proxy by workly
const worker = compose(
  proxy,
  transformEvalableToUrl,
  formatFuntionToEvalable
)(workerFunc);

export default worker;



