export const formatFuntionToEvalable = func => {
  const code = func.toString();
  const formatedCode = code.substring(
    code.indexOf('{') + 1,
    code.lastIndexOf('}')
  );
  return formatedCode;
};

export const transformEvalableToUrl = evalable => {
  const blob = new Blob([evalable], { type: 'application/javascript' });
  const script = URL.createObjectURL(blob);
  return script;
};


