/*global workly, importScripts, moment, axios, fetchJsonp */
const workercode = () => {
  const window = self;

  // import your alias like moment and axios
  // moment and axios scripts
  importScripts('https://cdn.bootcss.com/moment.js/2.22.1/moment.js');
  importScripts('https://cdn.bootcss.com/axios/0.18.0/axios.min.js');

  // code of worker
  const code =
    "var workly=function(a){'use strict';function b(a,c){return c=c||[],new Proxy(function(){},{get(d,e,f){if('then'===e){if(0===c.length)return{then:()=>f};const b=a.remote({type:'GET',path:c});return b.then.bind(b)}return b(a,c.concat(e))},set(b,d,e){return a.remote({type:'SET',path:c.concat(d),value:e})},apply(b,d,e){return a.remote({type:'APPLY',path:c,args:e})},construct(b,c){return a.remote({type:'CONSTRUCT',args:c})}})}function c(){return Math.floor(Math.random()*Number.MAX_SAFE_INTEGER)}function d(a){const b={},d=(a)=>{const d=`${Date.now()}-${c()}`;return b[d]=a,d};self.addEventListener('message',async(c)=>{let e=c.data||{};e.path=e.path||[];let f=e.target&&b[e.target]||a;const g=(a)=>a.reduce((a,b)=>a?a[b]:a,f),h=e&&e.id;if(h&&e.type){const a={id:h},b=g(e.path),c=g(e.path.slice(0,-1));switch(e.type){case'GET':a.value=b;break;case'SET':let f=e.path.length&&e.path[e.path.length-1];f&&(c[f]=e.value),a.value=!!f;break;case'APPLY':try{a.value=await b.apply(c,e.args||[])}catch(b){a.error=b.toString()}break;case'CONSTRUCT':try{a.value=new b(...e.args),a.targetId=d(a.value)}catch(b){a.error=b.toString()}}self.postMessage(a)}})}class e{constructor(a,d){this.w=a,this.uid=d||`${Date.now()}-${c()}`,this.c=0,this.cbs={},a.addEventListener('message',(c)=>{if(this.w.oURL)try{URL.revokeObjectURL(this.w.oURL)}catch(a){}finally{delete this.w.oURL}let d=c.data&&c.data.id,f=d&&this.cbs[d];f&&(delete this.cbs[d],c.data.error?f[1](new Error(c.data.error)):f[0](c.data.targetId?b(new e(a,c.data.targetId)):c.data.value))})}remote(a){const b=a.args||[],c=`${this.uid}-${++this.c}`;return new Promise((d,e)=>{this.cbs[c]=[d,e],this.w.postMessage(Object.assign({},a,{id:c,args:b,target:this.uid}))})}}return a.proxy=function(a){let f,g;if('function'==typeof a){const b=Function.prototype.toString;f=g=URL.createObjectURL(new Blob([`${b.call(c)}\n(${b.call(d)})(${b.call(a)})`]))}else'string'==typeof a&&(f=a,0===a.indexOf('blob:')&&(g=f));if(f){let a=new Worker(f);return g&&(a.oURL=g),b(new e(a))}throw'Workly only supports functions, classes, urls'},a.expose=d,a}({});";

  eval(code);

  function hello(message) {
    console.log('hello world', message);
    console.log('axios', axios);
    console.log('moment', moment);
    return ~~(Math.random() * 1000000);
  }
  function fetchExample(params) {
    return Promise.resolve({ hehe: 1, data: { hehe: 2 } }).then(res => {
      return res.data;
    });
  }
  function costly(times) {
    let count = 1;
    const start = Date.now();
    while (Date.now() - start < times) {
      count++;
    }
    return count;
  }

  // declare your function
  function fetchGithub(params) {
    const itemParser = ({
      watchers_count,
      stargazers_count,
      forks_count,
      created_at,
      full_name,
      url,
      id
    }) => ({
      id,
      key: id,
      watchers: watchers_count,
      stargazers: stargazers_count,
      forks: forks_count,
      createdAt: created_at,
      fullName: full_name,
      url
    });
    const dataParser = ({ total_count, items }) => ({
      total: total_count,
      items: items.map(itemParser)
    });
    return axios
      .get('https://api.github.com/search/repositories', { params })
      .then(res => res.data)
      .then(dataParser);
  }
  // expose your functions with workly
  workly.expose({ hello, fetchExample, costly, fetchGithub });
};

export default workercode;



